* {
  box-sizing: border-box;
  outline: 0;
  margin: 0;
  padding: 0;
  font-family: Mark;
}

body {
  overflow: hidden;
}

a, a:visited, a:hover, a:focus, a:active {
  color: inherit;
  text-decoration: none;
}

.title {
  letter-spacing: -1.16px;
  font-size: 58px;
  font-weight: 900;
}

.title span {
  color: #2546bc;
}

.subtitle {
  letter-spacing: -.3px;
  opacity: .5;
  font-size: 15px;
  font-weight: 400;
  line-height: 20px;
}

.btn {
  cursor: pointer;
  background-color: #2546bc;
  border: none;
  border-radius: 4px;
  padding: 18px 48px 21px;
  font-size: 13px;
  font-weight: 900;
}

.play {
  cursor: pointer;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: absolute;
  top: 200px;
  left: 100px;
  transform: translateX(-50%);
}

.play__circle {
  background-color: #fff;
  border-radius: 100%;
  width: 68px;
  height: 68px;
  position: relative;
}

.play__circle svg {
  width: 14px;
  height: 16px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%)translateX(-30%);
}

.play__text {
  color: #fff;
  letter-spacing: 3.06px;
  text-transform: uppercase;
  margin-left: 20px;
  font-size: 16px;
  font-weight: 900;
}

.hidden {
  visibility: hidden;
}

.menu {
  background: none;
