}

.sidecontrol {
  border-right: 1px solid #0000001a;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 84px;
  height: 100vh;
  padding: 40px 0;
  display: flex;
  position: absolute;
  top: 0;
}

.sidecontrol__controls {
  justify-content: space-between;
  align-items: center;
  min-width: 200px;
  display: flex;
  transform: rotate(-90deg)translateX(90px);
}

.sidecontrol__controls span, .sidecontrol__controls svg, .sidecontrol__controls a {
  display: block;
}

.sidecontrol__controls a svg {
  width: 20px;
  height: 24px;
